import {ref, onMounted} from "vue"

export function useCity(city){
    const cityInfo = ref([])
    const fetching = async() => {
        try {
            var url = "https://en.wikipedia.org/w/api.php";
            var params = {
                action: 'query',
                list: 'search',
                srsearch: city,
                format: 'json'
            };

            url = url + '?origin=*';
            Object.keys(params).forEach((key) => {
                url += "&" + key + "=" + params[key];
            });
            fetch(url)
            .then(
            function (response) {
                return response.json();
            }
            )
            .then(
                function(response){
                    cityInfo.value = response
                }
            )
        }
        catch(error) {
            console.log(error)
        }




    }
    fetching()
    return {
        cityInfo
    }
}